import React from 'react';
import { Box, SimpleGrid } from '@chakra-ui/react';
import ProductCard from '..//components/Product/ProductCard'; // ,     
import CatalogHeader from '..//components/Product/CatalogHeader';

const CatalogPage = () => {
  //  
  const products = [
    {
      id: 1,
      name: { en: "Sourdough Bread", ru: "  ", uk: "  ", pl: "Chleb na zakwasie" },
      description: { en: "Artisan sourdough bread.", ru: "   .", uk: "   .", pl: "Rzemielniczy chleb na zakwasie." },
      price: 5.99,
      imageUrl: "/images/bread.png"
    },
    //     
  ];

  return (
    <Box padding="4">
       <CatalogHeader />
      <SimpleGrid columns={{ base: 1, md: 3 }} spacing="40px">
        {products.map(product => (
          <ProductCard key={product.id} product={product} />
        ))}
      </SimpleGrid>
    </Box>
  );
};

export default CatalogPage;
